/* -------------------- Google Font -------------------- */
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700&display=swap');

/* -------------------- CSS Reset -------------------- */
@import-normalize;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}

button{
  cursor: pointer;
  background: transparent;
  border: none;
}
  
button:hover, button:focus{
  outline: none;
}

html{
  font-size: 16px;
  font-family: 'Montserrat', sans-serif;
}

body{
  font-family: inherit;
}

#root{
  height: auto;
  display: flex;
}


@media only screen and (min-width: 426px) {
  body {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  #root{
    width: 375px;
    height: 667px;
    box-shadow: 0px 5px 25px 0px rgba(0,0,0,0.15);
  }
}

@media only screen and (min-width: 426px) and (min-height: 812px) {
  #root{
    height: 812px;
  }
}

@media screen and (orientation: landscape) and (max-width: 832px) and (max-height: 426px) {
  body, #root{
    width: 100%;
    height: 100vh;
  }
}

/* -------------------- Animation -------------------- */
/* --------------- fade --------------- */
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  
  to {
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fade {
  animation-name: fadeOut;
  animation-name: fadeIn;
  animation-duration: 1.5s;
}

/* --------------- fadeInDown --------------- */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInDown {
  animation-name: fadeInDown;
  animation-duration: 1.5s;
  animation-delay: 0.5s;
}

/* --------------- shake --------------- */
@keyframes shake {
  from, to {
    transform: translate3d(0, 0, 0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-10px, 0, 0);
  }

  20%, 40%, 60%, 80% {
    transform: translate3d(10px, 0, 0);
  }
}

.shake {
  animation-name: shake;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
}

/* --------------- flash --------------- */
@keyframes flash {
  from, 50%, to {
    opacity: 1;
  }

  25%, 75% {
    opacity: 0;
  }
}

.flash {
  animation-name: flash;
  animation-duration: 2s;
  animation-delay: 2s;
}

.animated {
  animation-fill-mode: both;
  animation-timing-function: ease-in-out;
}