@import '../../../../Default.scss';

.TempForecast{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    flex: 0 0 auto;

    margin: 1rem 0;
}

.Date{
    font-size: 1rem;
    font-weight: 600;
    color: $black;
}

.ForecastContainer{
    @extend %flex-center-spaceBetween;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
}

.ForecastItem{
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    
    margin-top: 0.75rem;
    margin-right: 0.75rem;
}

.Time, .ForecastTemp{
    font-size: 0.875rem;
    font-weight: normal;
    color: $grey;
}

.ForecastIcon{
    margin: 0.5rem 0;

    i{
        font-size: $small;
    }
}