/* -------------------- Variables -------------------- */
/* --------------- Colors --------------- */
$white: #FAFAFA;
$lightestGrey: #F5F5F5;
$lighterGrey: #EEEEEE;
$lightGrey: #BDBDBD;
$grey: #757575;
$darkGrey: #424242;
$black: #212121;
$blue: #1E88E5;
$red: #E53935;
$green: #43A047;

/* --------------- Temperature --------------- */
$hot: #EF6C00;
$warmest: #F57C00;
$warmer: #FB8C00;
$warm: #FF9800;
$cold: #5FA0CB;
$colder: #4490C2;
$coldest: #2980B9;
$iceCold: #2470A2;

/* --------------- Sizes --------------- */
$extraSmall: 1.25rem;
$small: 1.5rem;
$medium: 2.25rem;
$large: 3.375rem;
$extraLarge: 4.25rem;

/* --------------- Margin & Padding --------------- */
$padding: 1.5rem;
$margin: 1.5rem;

/* --------------- Transition --------------- */
$transition: 0.5s ease-in-out;

/* --------------- Border-Radius --------------- */
$borderRadius: 10px;

/* -------------------- Extend/Inheritance -------------------- */
%flex-center-spaceBetween{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

%header-style{
  @extend %flex-center-spaceBetween;
  margin-bottom: $margin;
}

%button-style{
  width: 100%;
  cursor: pointer;

  background: $lighterGrey;
  border-radius: $borderRadius;

  padding: 0.75rem;

  font-family: inherit;
  font-size: 1rem;
  font-weight: 600;
  color: $blue;
  
  transition: $transition;

  &:hover, &:focus{
    background: $lightGrey;
  }
}

%button-icon-style{
  cursor: pointer;

  font-family: inherit;
  font-size: 1rem;

  color: $black;

  transition: $transition;
}

%link-style{
    width: 100%;
    text-decoration: none;
}

/* -------------------- Multiple Used Selectors -------------------- */
.Hot{
  color: $hot;
}

.Warmest{
  color: $warmest;
}

.Warmer{
  color: $warmer;
}

.Warm{
  color: $warm;
}

.Cold{
  color: $cold;
}

.Colder{
  color: $colder;
}

.Coldest{
  color: $coldest;
}

.IceCold{
  color: $iceCold;
}