@import '../../Default.scss';

.Header{
    @extend %header-style;
}

.Button{
    @extend %button-icon-style;
}

.Active{
    font-weight: 600;
    color: $black;
}

.Inactive{
    font-weight: normal;
    color: $lightGrey;
}

.Container{
    height: calc(100% - 43px);
    max-width: 100vw;
    overflow-x: hidden;
    @extend %flex-center-spaceBetween;
    flex-flow: column;
    justify-content: space-evenly;
    text-align: center;
}

.Title{
    font-size: $medium;
    font-weight: 600;
    color: $black;
}

.Country{
    font-size: 1rem;
}