@import '../../Default.scss';

.Button{
    @extend %button-icon-style;
}

.Search{
    width: 100%;
    display: flex;
    align-items: stretch;
    flex-direction: column;
    margin: $margin 0;
}

.Header{
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    color: $black;
}

.TextField{
    flex: 1;

    margin-top: 0.5rem;
    margin-bottom: $margin;
    padding: 0.25rem 0.5rem;

    font-family: inherit;
    font-size: $extraSmall;
    font-weight: bold;
    color: $black;

    background: none;
    border: none;
    border-bottom: 1.5px solid $black;
    border-radius: 0;

    transition: $transition;
    
    &::placeholder{
        opacity: 1;
        color: $black;
    }

    &:hover, &:focus{
        outline: none;
        border-bottom-color: $blue;
    }
}

.Submit{
    @extend %button-style;
}

.Error{
    border-bottom-color: $red;
    color: $red;

    &:hover, &:focus{
        border-bottom-color: $red;
    }
}

.ErrorBtn{
    color: $red;
}

.Correct{
    border-bottom-color: $green;
    color: $green;

    &:hover, &:focus{
        border-bottom-color: $green;
    }
}

.CorrectBtn{
    color: $green;
}