@import '../../Default.scss';

.LocationButton{
    @extend %flex-center-spaceBetween;
    @extend %button-style;
    margin: 0.5rem 0;
    padding: 0.25rem 1rem;
}

.Link{
    @extend %link-style;
}

.Left{
    @extend %flex-center-spaceBetween;
}

.Icon{
    i{
        font-size: $small;
    }
}

.TitleContainer{
    @extend %flex-center-spaceBetween;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 1rem;
}

.Date{
    font-size: 0.75rem;
}

.Title{
    font-size: 1.125rem;
}

.Temp{
    font-size: $medium;
    font-weight: 600;
}

@media only screen and (min-width: 375px) {
    .Icon{
        i{
            font-size: 1.75rem;
        }
    }

    .Date{
        font-size: 0.875rem;
    }
    
    .Title{
        font-size: $extraSmall;
    }

    .Temp{
        font-size: 2.5rem;
        font-weight: 600;
    }
}