@import '../../Default.scss';

.Header{
    @extend %header-style;
}

.Button{
    @extend %button-icon-style;
}

.Active{
    font-weight: 600;
    color: $black;
}

.Inactive{
    font-weight: normal;
    color: $lightGrey;
}

.ClearBtn{
    text-decoration: underline;
    &:hover, &:focus{
        color: $red;
    }
}

.LocationsSection{
    width: 100%;
    max-height: calc(100% - 24px);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    
    overflow-y: hidden;
}

.LocationsHeader{
    width: 100%;
    @extend %flex-center-spaceBetween;
}

.PageHeader{
    font-size: $small;
    font-weight: 600;
    color: $black;
}

.LocationsContainer{
    width: 100%;
}

.Locations{
    width: 100%;
    @extend %flex-center-spaceBetween;
    flex-flow: column;

    height: 100%;
    overflow-y: auto;

    margin: 1rem 0;
}